import {mapGetters, mapState} from 'vuex';

export default {
    data() {
        return {
            longitude: '',
            latitude: '',
            list: [],
            loading: false,
            page: 1,
        }
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            stock: state => state.mallConfig.__wxapp_img.stock,
            bonusImg: state => state.mallConfig.__wxapp_img.bonus,
            userInfo: state => state.user.info,
        })
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.longitude = options.longitude;
        this.latitude = options.latitude;
        this.getList();
    },
    methods: {
        bind(item) {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '绑定中...'
            });
            that.$request({
                url: that.$api.community.bind,
                data: {
                    middleman_id: item.user_id
                }
            }).then(response=>{
                uni.hideLoading();
                uni.removeStorage({
                    key: 'middleman_info'
                });
                if(response.data.is_private) {
                    uni.showToast({
                        title: '切换成功',
                        icon: 'none',
                        duration: 1000
                    });
                    item.is_allow_change = 1;
                    uni.setStorage({
                        key: 'middleman_info',
                        data: item
                    })
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
                if(response.code == 0) {
                    uni.setStorage({
                        key: 'bind',
                        data: item.user_id,
                        success: function () {
                            setTimeout(function(){
                                uni.navigateBack({});
                            },1000)
                        },
                        fail() {
                            setTimeout(function(){
                                uni.navigateBack({});
                            },1000)
                        }
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.loading = true;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.community.middleman_list,
                data: {
                    longitude: this.longitude,
                    latitude: this.latitude,
                    page: this.page,
                }
            }).then(response=>{
                that.$hideLoading();
                that.loading = false;
                if(response.code == 0) {
                    that.list = response.data.list;
                    for(let i in  that.list) {
                        that.list[i].space = ~~that.list[i].distance +'m';
                        if(that.list[i].distance > 1000) {
                            that.list[i].space = (that.list[i].distance / 1000).toFixed(1) + 'km'
                        }
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.loading = false;
                that.$hideLoading();
            });
        },
    }
}
